<template lang="">
    <div id="container">
        <h2>event list</h2>
        <div class="in_wrap">
            <div class="content" v-for="(e,i) in events" :key="i">
                <h3>{{e.title}}</h3>
                <img :src="e.image" @click="modalOpen(i)" style="width: 250px; height: 250px;">
                <eventModal :modalShows="modalShow" :crt="crt" :event="events"
                @closeModal="modalClose"></eventModal>
            </div>
          
        </div>
    </div>
</template>
<script>
import event from '../assets/event.js'
import eventModal from './EventModal'
export default {
    data(){
        return{
            events:event,
            modalShow:false,
            crt:0
        }
    },
    methods: {
        modalOpen(i){
            this.crt=i;
            this.modalShow=!this.modalShow;
        },
        modalClose(){
            this.modalShow=false;
        }
    },
    components:{
        eventModal
    }
    
}
</script>
<style scoped>
#container {
 margin-bottom: 10rem
 }
 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 /* width: auto; */
 height: auto;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
</style>